<template>
  <div>
    <a-spin :spinning="loading">
      <base-header type="gradient-primary" class="pb-6 pb-8 pt-5 pt-md-8">
        <a-input-search size="large" placeholder="请输入关键字" @search="searchKey" />
      </base-header>
      <div class="container-fluid mt--7">
        <div class="row justify-content-center">
          <a-empty style="margin-top: 15%;margin-bottom: 15%" v-if="!list">
            <span slot="description"> 暂无数据 </span>
          </a-empty>
          <div class="col-lg-8 card-wrapper ct-example">
            <div class="card" style="margin-bottom: 20px" v-for="(item, index) in list" :key="index">
              <div class="card-header border-0">
                <div class="row align-items-center">
                  <div class="col">
                    <h3 class="mb-0">{{ index }}</h3>
                  </div>
                  <div class="col text-right">
                  </div>
                </div>
              </div>
              <!-- Card body -->
              <div class="card-body">
                <!-- List group -->
                <ul class="list-group list-group-flush list my--3">
                  <li class="list-group-item px-0" v-for="(t, i) in item" :key="i">
                    <div class="row align-items-center">
                      <div class="col-auto">
                        <i class="ni ni-books"></i>
                      </div>
                      <div class="col ml--2">
                        <h4 class="mb-0">
                          <a @click="showDrawer(t.id)">{{ t.title }}</a>
                        </h4>
                        <small>更新时间: {{ t.updated_at | formatDate }}</small>
                      </div>
                      <div class="col-auto">
                        <button type="button" @click="showDrawer(t.id)" class="btn btn-sm btn-primary">查看详情</button>
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </a-spin>
    <a-drawer width="70%"
              placement="right"
              :title="knowledge.title"
              :visible="visible" @close="onClose">
      <div class="markdown-body">
        <VueMarkdown :source="knowledge.body" v-highlight></VueMarkdown>
      </div>
    </a-drawer>
  </div>
</template>

<script>
import {getKnowledgeList} from "@/api/api";
import 'github-markdown-css/github-markdown.css';
import VueMarkdown from 'vue-markdown';

export default {
  name: "knowledge",
  components: {
    VueMarkdown // 注入组件
  },
  data() {
    return {
      list: [],
      knowledge: {},
      loading: true,
      visible: false,
      value: "",
      pStyle: {
        fontSize: '16px',
        color: 'rgba(0,0,0,0.85)',
        lineHeight: '24px',
        display: 'block',
        marginBottom: '16px',
      },
      pStyle2: {
        marginBottom: '24px',
      },
    }
  },
  mounted() {
    this.getData()
  },
  methods: {
    showDrawer(id) {
      getKnowledgeList({
        language: 'zh-CN',
        id: id
      }).then(res => {
        this.knowledge = res.data;
        this.visible = true;
      })
    },
    onClose() {
      this.visible = false;
    },
    searchKey(value){
      this.getData(value);
    },
    getData(value) {
      this.loading = true;
      getKnowledgeList({
        language: 'zh-CN',
        value: value
      }).then(res => {
        this.loading = false;
        this.list = res.data;
      })
    }
  },
  filters: {
    formatDate: function (value) {
      if (!value) {
        return false;
      }
      if (value.toString().length === 10) {
        value = value * 1000;
      }
      let date = new Date(value);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? ('0' + MM) : MM;
      let d = date.getDate();
      d = d < 10 ? ('0' + d) : d;

      return y + '-' + MM + '-' + d;
    },
  }
}
</script>

<style scoped>

</style>