<template>
  <nav
      class="navbar navbar-vertical fixed-left navbar-expand-md navbar-light bg-white"
      id="sidenav-main"
  >
    <div class="container-fluid">
      <!--Toggler-->
      <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          aria-label="Toggle navigation"
          @click="showSidebarTab()"
      >
        <slot>
          <span class="navbar-toggler-icon"></span>
        </slot>
      </button>
      <router-link class="navbar-brand" to="/">
        <h2>{{ config.app_name }}</h2>
      </router-link>

      <slot name="mobile-right">
        <ul class="nav align-items-center d-md-none">
          <base-dropdown class="nav-item" position="right">
            <template v-slot:title>
              <a
                  class="nav-link nav-link-icon"
                  href="#"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
              >
                <i class="ni ni-bell-55"></i>
              </a>
            </template>
            <a class="dropdown-item" href="#">Action</a>
            <a class="dropdown-item" href="#">Another action</a>
            <div class="dropdown-divider"></div>
            <a class="dropdown-item" href="#">Something else here</a>
          </base-dropdown>
          <base-dropdown class="nav-item" position="right">
            <template v-slot:title>
              <a class="nav-link" href="#" role="button">
                <div class="media align-items-center">
                  <span class="avatar avatar-sm rounded-circle">
                    <img
                        alt="Image placeholder"
                        :src="user.avatar_url"
                    />
                  </span>
                </div>
              </a>
            </template>

            <div class="dropdown-header noti-title">
              <h6 class="text-overflow m-0">Welcome!</h6>
            </div>
            <router-link to="/profile" class="dropdown-item">
              <i class="ni ni-single-02"></i>
              <span>个人中心</span>
            </router-link>
            <div class="dropdown-divider"></div>
            <a href="#!" class="dropdown-item">
              <i class="ni ni-user-run"></i>
              <span>退出登录</span>
            </a>
          </base-dropdown>
        </ul>
      </slot>
      <slot></slot>
      <div
          v-show="showSidebar"
          class="navbar-collapse collapse show"
          id="sidenav-collapse-main"
      >
        <div class="navbar-collapse-header d-md-none">
          <div class="row">
            <div class="col-6 collapse-brand">
               <span> {{ user.email }}</span>
            </div>
            <div class="col-6 collapse-close">
              <button
                  class="navbar-toggler"
                  type="button"
                  data-toggle="collapse"
                  aria-label="Toggle navigation"
                  @click="closeSidebar"
              >
                <slot>
                  <span></span>
                  <span></span>
                </slot>
              </button>
            </div>
          </div>
        </div>
        <ul class="navbar-nav">
          <slot name="links"></slot>
        </ul>
        <hr class="my-3"/>
        <h6 class="navbar-heading text-muted">订阅</h6>
        <ul class="navbar-nav">
          <slot name="links-2"></slot>
        </ul>
        <hr class="my-3"/>
        <h6 class="navbar-heading text-muted">财务</h6>
        <ul class="navbar-nav">
          <slot name="links-3"></slot>
        </ul>
        <hr class="my-3"/>
        <h6 class="navbar-heading text-muted">用户</h6>
        <ul class="navbar-nav">
          <slot name="links-4"></slot>
        </ul>
      </div>
    </div>
  </nav>
</template>
<script>
import byval from "@/utils/byval";

export default {
  name: "sidebar",
  components: {
  },
  data() {
    return {
      showSidebar: false
    }
  },
  props: {
    logo: {
      type: String,
      default: "img/brand/green.png",
      description: "Sidebar app logo",
    },
    autoClose: {
      type: Boolean,
      default: true,
      description:
          "Whether sidebar should autoclose on mobile when clicking an item",
    },
    config: {
      type: Object
    },
    user: {
      type: Object
    },
  },
  mounted() {
    byval.$on('sidebar', function () {
      if (this.showSidebar) {
        this.showSidebar = false;
      } else {
        this.showSidebar = true;
      }
    })
  },
  provide() {
    return {
      autoClose: this.autoClose,
    };
  },
  methods: {
    closeSidebar() {
      console.log(12132)
      this.showSidebar = false;
    },
    showSidebarTab(){
      if (this.showSidebar) {
        this.showSidebar = false;
      } else {
        this.showSidebar = true;
      }
    }
  },
  beforeUnmount() {
    if (this.showSidebar) {
      this.showSidebar = false;
    }
  },
};
</script>
