<template>
  <div>
    <a-spin :spinning="loading">
    <base-header type="gradient-primary" class="pb-6 pb-8 pt-5 pt-md-8">
      <div class="row">
        <div class="col-xl-3 col-lg-6">
          <stats-card
              title="会员到期"
              type="gradient-red"
              :sub-title="sub.expired_at | formatDate"
              icon="ni ni-circle-08"
              class="mb-4 mb-xl-0"
          >
            <template v-slot:footer>
              <span class="text-nowrap" v-if="sub.reset_day">距离重置流量还有 {{ sub.reset_day }} 天</span>
              <span class="text-nowrap" v-else>暂无数据</span>
            </template>
          </stats-card>
        </div>
        <div class="col-xl-3 col-lg-6">
          <stats-card
              title="使用流量"
              type="gradient-orange"
              :sub-title="sub.u | bytesToSize"
              icon="ni ni-chart-bar-32"
              class="mb-4 mb-xl-0"
          >
            <template v-slot:footer>
              <span class="text-nowrap">总计 {{ sub.transfer_enable | bytesToSize}} GB</span>
            </template>
          </stats-card>
        </div>
        <div class="col-xl-3 col-lg-6">
          <stats-card
              title="钱包余额"
              type="gradient-green"
              :sub-title="user.balance | money"
              icon="ni ni-money-coins"
              class="mb-4 mb-xl-0"
          >
            <template v-slot:footer>
              <span class="text-nowrap">可用佣金 {{ user.commission_balance | money }}</span>
            </template>
          </stats-card>
        </div>
        <div class="col-xl-3 col-lg-6">
          <stats-card
              title="代办工单"
              type="gradient-info"
              :sub-title="stat[1] > 0 ? stat[1] : '0'"
              icon="ni ni-chart-pie-35"
              class="mb-4 mb-xl-0"
          >
            <template v-slot:footer>
              <span class="text-nowrap">已关闭工单：0</span>
            </template>
          </stats-card>
        </div>
      </div>
    </base-header>
    <div class="container-fluid mt--7">
      <div class="row mb-4">
        <div class="col-xl-6">
          <card header-classes="bg-transparent" style="width: 100%">
            <template v-slot:header>
              <div class="row align-items-center">
                <div class="col">
                  <h6 class="text-uppercase text-muted ls-1 mb-1">
                    我的订阅
                  </h6>
                  <h5 class="h3 mb-0">订阅链接</h5>
                </div>
              </div>
            </template>
            <div class="chart-body" v-if="sub.plan_id">
              <base-dropdown class="sub-button">
                <template v-slot:title>
                  <base-button class="dropdown-toggle">
                    SSR 订阅
                  </base-button>
                </template>
                <a class="dropdown-item"
                   v-clipboard:copy="sub.subscribe_url + '&flag=shadowsocks'"
                   v-clipboard:success="copySublink">复制 SSR 订阅</a>
                <a class="dropdown-item" @click="importSublink('ssr')">一键导入 SSR</a>
              </base-dropdown>
              <base-dropdown class="sub-button">
                <template v-slot:title>
                  <base-button type="primary" class="dropdown-toggle">
                    Clash 订阅
                  </base-button>
                </template>
                <a class="dropdown-item"
                   v-clipboard:copy="sub.subscribe_url + '&flag=clash'"
                   v-clipboard:success="copySublink">复制 Clash 订阅</a>
                <a class="dropdown-item" @click="importSublink('clash')">一键导入 Clash</a>
              </base-dropdown>
              <base-dropdown class="sub-button">
                <template v-slot:title>
                  <base-button type="info" class="dropdown-toggle">
                    Shadowrocket 订阅
                  </base-button>
                </template>
                <a class="dropdown-item"
                   v-clipboard:copy="sub.subscribe_url + '&flag=shadowrocket'"
                   v-clipboard:success="copySublink">复制 Shadowrocket 订阅</a>
                <a class="dropdown-item" @click="importSublink('shadowrocket')">一键导入 Shadowrocket</a>
              </base-dropdown>
              <base-button class="sub-button" type="secondary" v-clipboard:copy="sub.subscribe_url + '&flag=surge'"
                           v-clipboard:success="copySublink">Surge 订阅</base-button>
              <base-button class="sub-button" style="background-color: #1171ef;border-color: #1171ef;"
                           v-clipboard:copy="sub.subscribe_url + '&flag=quantumult%20x'"
                           v-clipboard:success="copySublink"> 复制 Quantumult X 订阅</base-button>
              <base-dropdown class="sub-button">
                <template v-slot:title>
                  <base-button type="success" class="dropdown-toggle">
                    Quantumult 订阅
                  </base-button>
                </template>
                <a class="dropdown-item"
                   v-clipboard:copy="sub.subscribe_url + '&flag=quantumult'"
                   v-clipboard:success="copySublink">复制 Quantumult 订阅</a>
                <a class="dropdown-item" @click="importSublink('quantumult')">一键导入 Quantumult</a>
              </base-dropdown>
              <base-button class="sub-button" type="danger"
                           v-clipboard:copy="sub.subscribe_url + '&flag=v2ray'"
                           v-clipboard:success="copySublink"> 复制 V2Ray 订阅</base-button>
              <base-dropdown class="sub-button">
                <template v-slot:title>
                  <base-button type="warning" class="dropdown-toggle">
                    Surfboard 订阅
                  </base-button>
                </template>
                <a class="dropdown-item"
                   v-clipboard:copy="sub.subscribe_url + '&flag=surfboard'"
                   v-clipboard:success="copySublink">复制 Surfboard 订阅</a>
                <a class="dropdown-item" @click="importSublink('surfboard')">一键导入 Surfboard</a>
              </base-dropdown>
            </div>
            <div class="card-body text-center" v-else>
              <a-empty
                  image="https://gw.alipayobjects.com/mdn/miniapp_social/afts/img/A*pevERLJC9v0AAAAAAAAAAABjAQAAAQ/original"
                  :image-style="{height: '60px'}"
              >
                <span slot="description"> 暂无订阅 </span>
                <a-button type="primary" @click="$router.push({path: '/plan'})">
                  添加订阅
                </a-button>
              </a-empty>
            </div>
          </card>
        </div>
        <div class="col-xl-6">
          <card header-classes="bg-transparent" style="width: 100%">
            <template v-slot:header>
              <div class="row align-items-center">
                <div class="col">
                  <h6 class="text-uppercase ls-1 mb-1">公告列表</h6>
                  <h5 class="h3 mb-0">公告列表</h5>
                </div>
              </div>
            </template>
            <div class="chart-area">
              <a-empty v-if="!noticeList">
                <span slot="description"> 暂无数据 </span>
              </a-empty>
              <a-collapse v-model="activeKey" accordion v-else>
                <a-collapse-panel v-for="(item, index) in noticeList" :key="index" :header="item.title">
                  <p>{{ item.content }}</p>
                </a-collapse-panel>
              </a-collapse>
            </div>
          </card>
        </div>
      </div>
      <div class="row">
        <card type="secondary" header-classes="bg-transparent" style="width: 100%">
          <template v-slot:header>
            <div class="row align-items-center">
              <div class="col">
                <h6 class="text-uppercase ls-1 mb-1">流量详情</h6>
                <h5 class="h3 mb-0">最近7天使用情况</h5>
              </div>
            </div>
          </template>
          <div class="chart-area">
            <canvas :height="350" :id="salesChartID"></canvas>
          </div>
        </card>
      </div>
      <!-- End charts-->
    </div>
    <!--Tables-->

    <!--End tables-->
    </a-spin>
  </div>
</template>
<script>
// Charts
import {ordersChart} from "@/components/Charts/Chart";
import Chart from "chart.js";
import {getNoticeList, getSubscribe, getStat, getServerLogSeven} from "@/api/api";

let chart;
export default {
  components: {},
  data() {
    return {
      salesChartID: "salesChart",
      ordersChartID: "ordersChart",
      bigLineChart: {
        allData: [0, 20, 5, 25, 10, 30, 15, 40, 40],
        activeIndex: 0,
      },
      noticeList: [],
      activeKey: [0],
      sub: {},
      config: {},
      stat: [],
      loading: true
    };
  },
  props: {
    user: {
      type: Object
    }
  },
  methods: {
    initBigChart() {
      chart.destroy();
    },
    importSublink(type){
        let sub_url = this.sub.subscribe_url + '&flag=' + type;
        let client = {};
        client['surfboard'] = 'surfboard:///install-config?url=' + encodeURIComponent(sub_url);
        client['quantumult'] = 'quantumult://configuration?server=' + btoa(sub_url).replace(/=/g, '') + '&filter=YUhSMGNITTZMeTl0ZVM1dmMyOWxZMjh1ZUhsNkwzSjFiR1Z6TDNGMVlXNTBkVzExYkhRdVkyOXVaZw';
        client['shadowrocket'] = 'shadowrocket://add/sub://' + btoa(sub_url) + '?remarks=' + this.config.app_name;
        client['surge'] = 'surge:///install-config?url=' + encodeURIComponent(sub_url);
        client['surge3'] = 'surge3:///install-config?url=' + encodeURIComponent(sub_url);
        client['clash'] = 'clash://install-config?url=' + encodeURIComponent(sub_url);
        client['ssr'] = 'sub://' + btoa(sub_url);
        window.location.href = client[type];
    },
    copySublink(){
      this.$notification.open({
        message: '复制成功',
        icon: <a-icon type="smile" style="color: #108ee9" />,
      });
    },
    getData() {
      getNoticeList().then(res => {
        this.noticeList = res.data;
      });
      getSubscribe().then(res => {
        this.sub = res.data;
      })
      getStat().then(res => {
        this.stat = res.data;
      })
      getServerLogSeven().then(res => {
        let labels = [];
        let data = [];
        res.data.forEach(function (item){
          labels.push(item.date_time);
          data.push((item.ud / 1073741824).toPrecision(3));
        });
        this.loading = false;
        this.bigLineChart.allData = res.data;
        chart = new Chart(
            document.getElementById(this.salesChartID).getContext("2d"),
            {
              type: "line",
              data: {
                labels: labels,
                datasets: [
                  {
                    label: "使用流量GB",
                    tension: 0.4,
                    borderWidth: 4,
                    borderColor: "#5e72e4",
                    pointRadius: 0,
                    backgroundColor: "transparent",
                    data: data,
                  },
                ],
              },
              options: {
                responsive: true,
                maintainAspectRatio: false,
                tooltips: {
                  enabled: true,
                  mode: "index",
                  intersect: false,
                },
                scales: {
                  yAxes: [
                    {
                      barPercentage: 1.6,
                      gridLines: {
                        drawBorder: false,
                        color: "rgba(29,140,248,0.0)",
                        zeroLineColor: "transparent",
                      },
                      ticks: {
                        padding: 0,
                        fontColor: "#8898aa",
                        fontSize: 13,
                        fontFamily: "Open Sans",
                      },
                    },
                  ],
                  xAxes: [
                    {
                      barPercentage: 1.6,
                      gridLines: {
                        drawBorder: false,
                        color: "rgba(29,140,248,0.0)",
                        zeroLineColor: "transparent",
                      },
                      ticks: {
                        padding: 10,
                        fontColor: "#8898aa",
                        fontSize: 13,
                        fontFamily: "Open Sans",
                      },
                    },
                  ],
                },
                layout: {
                  padding: 0,
                },
              },
            }
        );
        ordersChart.createChart(this.ordersChartID);
      })
    }
  },
  mounted() {
    this.config = JSON.parse(localStorage.getItem('config'));
    this.getData();
  },
  filters: {
    formatDate: function (value) {
      if (! value){
        return "暂无数据";
      }
      if (value.toString().length === 10) {
        value = value * 1000;
      }
      let date = new Date(value);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? ('0' + MM) : MM;
      let d = date.getDate();
      d = d < 10 ? ('0' + d) : d;

      return y + '-' + MM + '-' + d;
    },
    bytesToSize(bytes) {
      if (bytes === 0) return '0 B';
      var k = 1024, // or 1024
          sizes = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'],
          i = Math.floor(Math.log(bytes) / Math.log(k));

      return (bytes / Math.pow(k, i)).toPrecision(3) + ' ' + sizes[i];
    },
    money(value){
      return '￥' + (value / 100).toFixed(2);
    }
  }
};
</script>
<style>
.sub-button {
  margin: 5px;
}
</style>
