<template>
  <div>
    <a-spin :spinning="loading">
      <base-header type="gradient-primary" class="pb-6 pb-8 pt-5 pt-md-8">

      </base-header>
      <div class="container-fluid mt--7">
        <div class="row justify-content-center">
          <div class="col-lg-8 card-wrapper ct-example">
            <div class="card" style="margin-bottom: 20px">
              <div class="card-header bg-transparent">
                <div class="row align-items-center">
                  <div class="col">
                    <h3 class="mb-0">订单列表</h3>
                  </div>
                  <div class="col text-right">
                  </div>
                </div>
              </div>
              <!-- Card body -->
              <div class="card-body">
                <a-table :columns="columns"
                         :data-source="list"
                         :loading="loading">
                  <span slot="cycle" slot-scope="cycle">
                    <span class="text-primary">{{ cycleList[cycle] }}</span>
                  </span>
                  <span slot="total_amount" slot-scope="total_amount"> {{ total_amount | money }} </span>
                  <span slot="status" slot-scope="status" class="badge badge-dot">
                      <i :class="statusLabel[status]"></i>
                      {{ statusText[status] }}
                  </span>
                  <span slot="created_at" slot-scope="created_at"> {{ created_at | formatDate }} </span>
                  <span slot="action" slot-scope="text, record">
                    <div class="editable-row-operations">
                      <span>
                        <a class="text-primary" @click="cancelOrder(record.trade_no)" v-if="record.status === 0">取消订单</a>
                      </span>
                    </div>
                  </span>
                </a-table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </a-spin>
  </div>
</template>

<script>
import {getOrderList} from "@/api/api";
import {cancelOrder} from "../api/api";

export default {
  name: "knowledge",
  components: {},
  data() {
    return {
      list: [],
      columns: [
        {
          title: '订单号',
          dataIndex: 'trade_no',
          scopedSlots: { customRender: 'trade_no' },
        },
        {
          title: '周期',
          dataIndex: 'cycle',
          scopedSlots: { customRender: 'cycle' },
        },
        {
          title: '订单金额',
          dataIndex: 'total_amount',
          scopedSlots: { customRender: 'total_amount' },
        },
        {
          title: '订单状态',
          dataIndex: 'status',
          scopedSlots: { customRender: 'status' },
        },
        {
          title: '创建时间',
          dataIndex: 'created_at',
          scopedSlots: { customRender: 'created_at' },
        },
        {
          title: '操作',
          dataIndex: 'action',
          scopedSlots: { customRender: 'action' },
        }
      ],
      loading: true,
      cycleList: {
        month_price: '月付',
        quarter_price: '季付',
        half_year_price: '半年付',
        year_price: '年付',
        two_year_price: '两年付',
        three_year_price: '三年付',
        onetime_price: '一次性',
      },
      statusText:['待支付', '开通中', '已取消', '已完成', '已折抵'],
      statusLabel:['bg-default', 'bg-primary', 'bg-danger', 'bg-info', 'bg-success', 'bg-warning'],
    }
  },
  mounted() {
    this.getData()
  },
  methods: {
    getData() {
      getOrderList().then(res => {
        this.loading = false;
        this.list = res.data;
      })
    },
    cancelOrder(trade_no){
      cancelOrder({trade_no: trade_no}).then(res => {
        if (res.data){
          this.$message.success('取消成功');
          this.getData();
        }
      })
    }
  },
  filters: {
    formatDate: function (value) {
      if (!value) {
        return false;
      }
      if (value.toString().length === 10) {
        value = value * 1000;
      }
      let date = new Date(value);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? ('0' + MM) : MM;
      let d = date.getDate();
      d = d < 10 ? ('0' + d) : d;
      let h = date.getHours();
      h = h < 10 ? ('0' + h) : h;
      let m = date.getMinutes();
      m = m < 10 ? ('0' + m) : m;
      let s = date.getSeconds();
      s = s < 10 ? ('0' + s) : s;

      return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
    },
    money(value) {
      return '￥' + (value / 100).toFixed(2);
    }
  }
}
</script>

