<template>
  <div>
    <a-spin :spinning="loading">
      <base-header type="gradient-primary" class="pb-6 pb-8 pt-5 pt-md-8">

      </base-header>
      <div class="container-fluid mt--7">
        <div class="row justify-content-center">
          <div class="card mb-4 col-sm-12 col-xl-8 mb-3">
            <div class="card-header">
              <h3 class="mb-0">商品详情</h3>
            </div>
            <div class="card-body">
              <h3>{{ data.plan.name }}</h3>
              <p>流量：{{ data.plan.transfer_enable }} GB</p>
              <p v-html="data.plan.content"></p>
            </div>
          </div>
        </div>
        <div class="row justify-content-center" v-if="!showResult">
          <div class="card mb-4 col-sm-12 col-xl-8 mb-3">
            <div class="card-header">
              <h5 class="h3">订单详情</h5>
              <h6 class="text-uppercase text-muted ls-1 mb-1"> # {{ data.trade_no }}</h6>
            </div>
            <div class="card-body">
              <div class="row">
                <div class="col-xl-6">
                  <div>
                    <span style="font-size: large;">{{ data.plan.name }} x {{ cycleList[data.cycle] }}</span>
                    <span style="font-size: large;float: right">{{ data.plan[data.cycle] | money }}</span>
                  </div>
                  <div v-if="data.discount_amount">
                    <hr class="my-3"/>
                    <span style="font-size: large;">抵扣金额</span>
                    <span style="font-size: large;float: right">{{ data.discount_amount | money }}</span>
                  </div>
                  <div v-if="data.balance_amount">
                    <hr class="my-3"/>
                    <span style="font-size: large;">抵扣金额</span>
                    <span style="font-size: large;float: right">{{ data.balance_amount | money }}</span>
                  </div>
                  <div style="margin-top: 1rem">
                    <hr class="my-3"/>
                    <h2 style="font-size: large;float: right">总计：{{ data.total_amount | money }}</h2>
                  </div>
                </div>
                <div class="col-xl-6">
                  <p v-if="payment_method">支付方式：</p>
                  <a-radio-group v-model="payment">
                    <a-radio-button
                        v-for="(item, index) in payment_method"
                        :key="index"
                        :value="item.id"
                        class="shop-button">
                      <h3>{{ item.name }}</h3>
                    </a-radio-button>
                  </a-radio-group>
                </div>
              </div>
            </div>
            <div class="card-footer">
              <button class="btn btn-icon btn-primary" type="button" style="float: right" @click="pay">
                <span class="btn-inner--icon"><i class="ni ni-bag-17"></i></span>
                <span class="btn-inner--text">立即支付</span>
              </button>
            </div>
          </div>
        </div>
        <div class="row justify-content-center" v-if="showResult">
          <div class="card mb-4 col-sm-12 col-xl-8 mb-3">
            <div class="card-header">
              <h5 class="h3">订单详情</h5>
              <h6 class="text-uppercase text-muted ls-1 mb-1"> # {{ data.trade_no }}</h6>
            </div>
            <div class="card-body text-center">
              <img src="../assets/image/pay-success.svg" width="84" style="margin: 1rem 0">
              <p style="font-size: 2rem;font-weight: 400;">开通中</p>
              <p>订单系统正在进行处理，请稍等1-3分钟。</p>
            </div>
          </div>
        </div>
      </div>
    </a-spin>
  </div>
</template>

<script>
import {getOrderDetail, getPaymentMethod, checkOrder, checkoutOrder} from "@/api/api";

export default {
  name: "knowledge",
  data() {
    return {
      data: {},
      loading: true,
      cycle: 'month_price',
      cycle_text: '月付',
      cycleList: {
        month_price: '月付',
        quarter_price: '季付',
        half_year_price: '半年付',
        year_price: '年付',
        two_year_price: '两年付',
        three_year_price: '三年付',
        onetime_price: '一次性',
      },
      discount_price: 0,
      price: 0,
      total: 0,
      order_id: 0,
      coupon_code: '',
      payment: '',
      showResult: false,
      payment_method: []
    }
  },
  mounted() {
    this.order_id = this.$route.params.id;
    this.getData(this.order_id);
    var trade_no = this.order_id;
    window.clearInterval();
    let that = this;
    window.setInterval(function (){
      if (trade_no){
        checkOrder({trade_no: trade_no}).then(res => {
          if (res.data){
            that.showResult = true;
            window.clearInterval();
          }
        })
      }
    }, 3000, trade_no);
  },
  methods: {
    getData(id) {
      getOrderDetail({trade_no: id}).then(res => {
        this.loading = false;
        this.data = res.data;
      })
      getPaymentMethod().then(res => {
        this.payment_method = res.data;
        if (res.data){
          this.payment = res.data[0]['id']
        }
      })
    },
    pay(){
      checkoutOrder({
        trade_no: this.order_id,
        method: this.payment,
      }).then(res => {
        if (res.data && res.type === 1){
          window.clearInterval();
          window.location.href = res.data;
        } else if(res.data && res.type === -1){
          this.showResult = true;
          window.clearInterval();
        }
      })
    }
  },
  filters: {
    formatDate: function (value) {
      if (!value) {
        return false;
      }
      if (value.toString().length === 10) {
        value = value * 1000;
      }
      let date = new Date(value);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? ('0' + MM) : MM;
      let d = date.getDate();
      d = d < 10 ? ('0' + d) : d;

      return y + '-' + MM + '-' + d;
    },
    money(value) {
      return '￥' + (value / 100).toFixed(2);
    }
  }
}
</script>

<style scoped>
.shop-button {
  margin: 0.5rem;
  padding: 10px 20px 5px;
  height: unset;
  box-shadow: 0 2px 10px 0 rgb(0 0 0 / 5%);
  font-size: 16px;
  border-radius: 5px !important;
  border: 1px solid #d9d9d9;
  text-align: center;
}
</style>