<template>
  <div>
    <a-spin :spinning="loading">
      <base-header type="gradient-primary" class="pb-6 pb-8 pt-5 pt-md-8">

      </base-header>
      <div class="container-fluid mt--7">
        <div class="row justify-content-center">
          <div class="col-lg-8 card-wrapper ct-example">
            <div class="card" style="margin-bottom: 20px">
              <div class="card-header bg-transparent">
                <div class="row align-items-center">
                  <div class="col">
                    <h3 class="mb-0">工单列表</h3>
                  </div>
                  <div class="col text-right">
                    <base-button icon="ni ni-air-baloon" type="primary" @click="modal1 = true">新建工单</base-button>
                  </div>
                </div>
              </div>
              <!-- Card body -->
              <div class="card-body">
                <a-table :columns="columns"
                         :data-source="list"
                         :loading="loading">
                  <span slot="level" slot-scope="level">
                    <badge rounded type="primary" v-if="level === 0">低级</badge>
                    <badge rounded type="warning" v-else-if="level === 1">中级</badge>
                    <badge rounded type="danger" v-else-if="level === 2">高级</badge>
                  </span>
                  <span slot="status" slot-scope="status" class="badge badge-dot">
                      <i :class="statusLabel[status]"></i>
                      {{ statusText[status] }}
                  </span>
                  <span slot="created_at" slot-scope="created_at"> {{ created_at | formatDate }} </span>
                  <span slot="updated_at" slot-scope="updated_at"> {{ updated_at | formatDate }} </span>
                  <span slot="action" slot-scope="text, record">
                    <a class="ant-dropdown-link" @click="showTicket(record.id)">查看</a>
                    <a-divider type="vertical" v-if="record.status === 0"/>
                    <a class="ant-dropdown-link" v-if="record.status === 0" @click="closeTicket(record.id)">关闭</a>
                  </span>
                </a-table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </a-spin>
    <modal :show="modal1"
           :showClose="false"
           body-classes="p-0"
           modal-classes="modal-dialog-centered">
      <template v-slot:header>
        <h4 class="modal-title" id="modal-title-notification">新建工单</h4>
      </template>
      <div class="p-3">
        <div class="form-group has-label">
          <label class="form-control-label"> 主题 </label>
          <input v-model="form.subject" aria-describedby="addon-right addon-left" placeholder="请输入工单主题"
                 class="form-control">
        </div>
        <div class="form-group">
          <label class="form-control-label">工单等级</label>
          <div class="has-label">
            <select class="form-control" v-model="form.level">
              <option value="0">低级</option>
              <option value="1">中级</option>
              <option value="2">高级</option>
            </select>
          </div>
        </div>
        <div class="form-group has-label">
          <label class="form-control-label"> 消息 </label>
          <textarea v-model="form.message" class="form-control" id="exampleFormControlTextarea3" rows="3"
                    placeholder="请描述你遇到的问题"></textarea>
        </div>
      </div>
      <template v-slot:footer>
        <base-button type="link"
                     class="ml-auto"
                     @click="modal1 = false">
          取消
        </base-button>
        <base-button type="primary" @click="onSubmit1">提交</base-button>
      </template>
    </modal>
    <modal :show="modal2"
           :showClose="false"
           body-classes="p-0"
           modal-classes="modal-dialog-centered">
      <template v-slot:header>
        <h4 class="modal-title">工单回复</h4>
      </template>
      <div class="p-3">
        <a-timeline>
          <a-timeline-item
              v-for="(item, i) in messageList"
              :key="i" :color="i%2 === 0 || i === 0 ? 'green' : 'blue'">{{ item.message }}</a-timeline-item>
        </a-timeline>
      </div>
      <template v-slot:footer>
        <input v-model="reply_text" aria-describedby="addon-right addon-left"
               @keyup.enter="replyTicket"
               placeholder="请输入内容回复工单..."
               class="form-control">
        <base-button type="link"
                     class="ml-auto"
                     @click="modal2 = false">
          关闭
        </base-button>
      </template>
    </modal>
  </div>
</template>

<script>
import {getTicketList, saveTicket, closeTicket, getTicketReplyList, replyTicket} from "@/api/api";

export default {
  name: "knowledge",
  components: {},
  data() {
    return {
      list: [],
      messageList: [],
      columns: [
        {
          title: '#',
          dataIndex: 'id',
          scopedSlots: {customRender: 'id'},
        },
        {
          title: '主题',
          dataIndex: 'subject',
          scopedSlots: {customRender: 'subject'},
        },
        {
          title: '工单级别',
          dataIndex: 'level',
          scopedSlots: {customRender: 'level'},
        },
        {
          title: '工单状态',
          dataIndex: 'status',
          scopedSlots: {customRender: 'status'},
        },
        {
          title: '创建时间',
          dataIndex: 'created_at',
          scopedSlots: {customRender: 'created_at'},
        },
        {
          title: '最后回复',
          dataIndex: 'updated_at',
          scopedSlots: {customRender: 'updated_at'},
        }, {
          title: '操作',
          dataIndex: 'action',
          scopedSlots: {customRender: 'action'},
        }
      ],
      modal1: false,
      modal2: false,
      loading: true,
      form: {
        subject: '',
        level: 0,
        message: '',
      },
      reply_text: '',
      reply_id: '',
      statusText: ['待回复', '已关闭', '已取消', '已完成', '已折抵'],
      statusLabel: ['bg-danger', 'bg-success', 'bg-danger', 'bg-info', 'bg-success', 'bg-warning'],
    }
  },
  mounted() {
    this.getData()
  },
  methods: {
    getData() {
      getTicketList().then(res => {
        this.loading = false;
        this.list = res.data;
      })
    },
    onSubmit1() {
      if (!this.form.subject) {
        this.$notification.warning({message: '请输入主题名称'});
        return false;
      }
      if (!this.form.message) {
        this.$notification.warning({message: '请输入消息主题内容'});
        return false;
      }
      saveTicket(this.form).then(res => {
        if (res.data) {
          this.modal1 = true;
          this.$message.success('提交成功，请等待~');
        }
      })
    },
    closeTicket(id) {
      closeTicket({id: id}).then(res => {
        if (res.data) {
          this.$message.success('关闭成功~');
          this.getData();
        }
      })
    },
    replyTicket() {
      replyTicket({id: this.reply_id, message: this.reply_text}).then(res => {
        if (res.data) {
          this.$message.success('回复成功~');
          this.getData();
        }
      })
    },
    showTicket(id) {
      this.reply_id = id;
      this.modal2 = true;
      getTicketReplyList({id: id}).then(res => {
        if (res.data) {
          this.messageList = res.data.message;
        }
      });
    }
  },
  filters: {
    formatDate: function (value) {
      if (!value) {
        return false;
      }
      if (value.toString().length === 10) {
        value = value * 1000;
      }
      let date = new Date(value);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? ('0' + MM) : MM;
      let d = date.getDate();
      d = d < 10 ? ('0' + d) : d;
      let h = date.getHours();
      h = h < 10 ? ('0' + h) : h;
      let m = date.getMinutes();
      m = m < 10 ? ('0' + m) : m;
      let s = date.getSeconds();
      s = s < 10 ? ('0' + s) : s;

      return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
    },
    money(value) {
      return '￥' + (value / 100).toFixed(2);
    }
  }
}
</script>

<style scoped>

</style>