<template>
  <div>
    <a-spin :spinning="loading">
      <base-header type="gradient-primary" class="pb-6 pb-8 pt-5 pt-md-8">

      </base-header>
      <div class="container-fluid mt--7">
        <div class="row justify-content-center">
          <div class="col-lg-8 card-wrapper ct-example">
            <div class="card" style="margin-bottom: 20px">
              <div class="card-header bg-transparent">
                <div class="row align-items-center">
                  <div class="col">
                    <h3 class="mb-0">订单列表</h3>
                  </div>
                  <div class="col text-right">
                  </div>
                </div>
              </div>
              <!-- Card body -->
              <div class="card-body">
                <div class="row ml-1 mb-3">
                  <a-radio-group v-model="param.type" @change="handleSizeChange">
                    <a-radio-button :value="0">
                      今日
                    </a-radio-button>
                    <a-radio-button :value="1">
                      本周
                    </a-radio-button>
                    <a-radio-button :value="2">
                      本月
                    </a-radio-button>
                  </a-radio-group>
                </div>
                <a-table :columns="columns"
                         :data-source="list"
                         :pagination="false"
                         :loading="loading2">
                  <span slot="u" slot-scope="u">
                    <span class="text-primary">{{ u | bytesToSize }}</span>
                  </span>
                  <span slot="d" slot-scope="d">
                    <span class="text-primary">{{ d | bytesToSize }}</span>
                  </span>
                  <span slot="total" slot-scope="text, record">
                    <span class="text-primary">{{ parseInt(record.u) + parseInt(record.d) | bytesToSize }}</span>
                  </span>
                  <span slot="rate" slot-scope="rate">
                    <a-tag>{{ rate + ' x'}}</a-tag>
                  </span>
                  <span slot="created_at" slot-scope="created_at"> {{ created_at | formatDate }} </span>
                </a-table>
                <div class="mt-3">
                  <a-pagination style="float: right"
                                @change="currentPage"
                                :default-current="param.current"
                                :total="param.total" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </a-spin>
  </div>
</template>

<script>
import {getServerLogList} from "@/api/api";

export default {
  name: "knowledge",
  components: {},
  data() {
    return {
      list: [],
      columns: [
        {
          title: '记录时间',
          dataIndex: 'created_at',
          scopedSlots: { customRender: 'created_at' },
        },
        {
          title: '实际上行',
          dataIndex: 'u',
          scopedSlots: { customRender: 'u' },
        },
        {
          title: '实际下行',
          dataIndex: 'd',
          scopedSlots: { customRender: 'd' },
        },
        {
          title: '倍率',
          dataIndex: 'rate',
          scopedSlots: { customRender: 'rate' },
        },
        {
          title: '合计',
          dataIndex: 'total',
          scopedSlots: { customRender: 'total' },
        }
      ],
      loading: true,
      loading2: true,
      param:{
        current: 1,
        pageSize: 10,
        total: 0,
        type: 0
      }
    }
  },
  mounted() {
    this.getData()
  },
  methods: {
    getData() {
      this.loading2 = true;
      getServerLogList(this.param).then(res => {
        this.loading2 = false;
        this.loading = false;
        this.list = res.data;
        this.param.total = res.total;
      })
    },
    currentPage(page, pageSize) {
      this.param.current = page;
      this.param.pageSize = pageSize;
      this.getData();
    },
    handleSizeChange(){
      this.getData();
    }
  },
  filters: {
    formatDate: function (value) {
      if (!value) {
        return false;
      }
      if (value.toString().length === 10) {
        value = value * 1000;
      }
      let date = new Date(value);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? ('0' + MM) : MM;
      let d = date.getDate();
      d = d < 10 ? ('0' + d) : d;
      let h = date.getHours();
      h = h < 10 ? ('0' + h) : h;
      let m = date.getMinutes();
      m = m < 10 ? ('0' + m) : m;
      let s = date.getSeconds();
      s = s < 10 ? ('0' + s) : s;

      return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
    },
    bytesToSize(bytes) {
      if (bytes === 0) return '0 B';
      var k = 1024, // or 1024
          sizes = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'],
          i = Math.floor(Math.log(bytes) / Math.log(k));

      return (bytes / Math.pow(k, i)).toPrecision(3) + ' ' + sizes[i];
    },
  }
}
</script>

<style scoped>

</style>