<template>
  <div>
    <a-spin :spinning="loading">
      <base-header type="gradient-primary" class="pb-6 pb-8 pt-5 pt-md-8">
        <div class="row">
          <div class="col-xl-3 col-lg-6">
            <stats-card
                title="用户"
                type="gradient-red"
                :sub-title="stat[0] > 0 ? stat[0] : '0'"
                icon="ni ni-circle-08"
                class="mb-4 mb-xl-0"
            >
              <template v-slot:footer>
                <span class="text-nowrap">已注册用户数</span>
              </template>
            </stats-card>
          </div>
          <div class="col-xl-3 col-lg-6">
            <stats-card
                title="比例"
                type="gradient-orange"
                :sub-title="stat[3] + '%'"
                icon="ni ni-money-coins"
                class="mb-4 mb-xl-0"
            >
              <template v-slot:footer>
                <span class="text-nowrap">佣金比例</span>
              </template>
            </stats-card>
          </div>
          <div class="col-xl-3 col-lg-6">
            <stats-card
                title="确认中佣金"
                type="gradient-green"
                :sub-title="stat[2] | money"
                icon="ni ni-money-coins"
                class="mb-4 mb-xl-0"
            >
              <template v-slot:footer>
                <span class="text-nowrap">
                  确认中的佣金
                  <a-tooltip placement="bottom">
                      <template slot="title">
                        佣金将会在确认后会到达你的佣金账户。
                      </template>
                     <a-icon type="question-circle"/>
                  </a-tooltip>
                </span>
              </template>
            </stats-card>
          </div>
          <div class="col-xl-3 col-lg-6">
            <stats-card
                title="可用佣金"
                type="gradient-info"
                :sub-title="stat[4] | money"
                icon="ni ni-money-coins"
                class="mb-4 mb-xl-0"
            >
              <template v-slot:footer>
                <span class="text-nowrap">可用佣金</span>
              </template>
            </stats-card>
          </div>
        </div>
      </base-header>
      <div class="container-fluid mt--7">
        <div class="row justify-content-center">
          <div class="col-lg-8 card-wrapper ct-example">
            <div class="card" style="margin-bottom: 20px">
              <div class="card-header bg-transparent">
                <div class="row align-items-center">
                  <div class="col-8">
                    <!-- Title -->
                    <h5 class="h3 mb-0">邀请码管理</h5>
                  </div>
                  <div class="col-4 text-right">
                    <a @click="createInviteCode" class="btn btn-primary">生成邀请码</a>
                  </div>
                </div>
              </div>
              <!-- Card body -->
              <div class="card-body">
                <a-table :columns="columns_1"
                         :data-source="code"
                         :pagination="false"
                         :loading="loading">
                  <span slot="created_at" slot-scope="created_at"> {{ created_at | formatDate }} </span>
                  <span slot="action" slot-scope="text, record">
                    <div class="editable-row-operations">
                      <span>
                        <a class="text-primary"
                            v-clipboard:copy="config.tos_url + '/#/register?code=' + record.code"
                            v-clipboard:success="copyInviteUrl">
                          复制链接
                        </a>
                      </span>
                    </div>
                  </span>
                </a-table>
              </div>
            </div>
          </div>
        </div>
        <div class="row justify-content-center">
          <div class="col-lg-8 card-wrapper ct-example">
            <div class="card" style="margin-bottom: 20px">
              <div class="card-header bg-transparent">
                <div class="row align-items-center">
                  <div class="col">
                    <h3 class="mb-0">邀请明细</h3>
                  </div>
                  <div class="col text-right">
                  </div>
                </div>
              </div>
              <!-- Card body -->
              <div class="card-body">
                <a-table :columns="columns_2"
                         :data-source="list"
                         :pagination="false"
                         :loading="loading">
                  <span slot="created_at" slot-scope="created_at"> {{ created_at | formatDate }} </span>
                  <span slot="commission_balance" slot-scope="commission_balance"> {{ commission_balance | money }} </span>
                  <span slot="commission_status" slot-scope="commission_status"> {{ status[commission_status] }} </span>
                </a-table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </a-spin>
  </div>
</template>

<script>
import {getInviteDetail, getInviteList, saveInviteCode} from "@/api/api";

export default {
  name: "knowledge",
  data() {
    return {
      stat: [],
      code: [],
      loading: true,
      list: [],
      config: {},
      status: ['待确认', '发放中', '已发送', '无效'],
      columns_1: [
        {
          title: '邀请码',
          dataIndex: 'code',
          scopedSlots: {customRender: 'code'},
        },
        {
          title: '创建时间',
          dataIndex: 'created_at',
          scopedSlots: {customRender: 'created_at'},
        },
        {
          title: '操作',
          dataIndex: 'action',
          scopedSlots: {customRender: 'action'},
        }
      ],
      columns_2: [
        {
          title: '#',
          dataIndex: 'id',
          scopedSlots: {customRender: 'id'},
        },
        {
          title: '完成时间',
          dataIndex: 'created_at',
          scopedSlots: {customRender: 'created_at'},
        },
        {
          title: '佣金',
          dataIndex: 'commission_balance',
          scopedSlots: {customRender: 'commission_balance'},
        },
        {
          title: '佣金状态',
          dataIndex: 'commission_status',
          scopedSlots: {customRender: 'commission_status'},
        }
      ],
    }
  },
  mounted() {
    this.config = JSON.parse(localStorage.getItem('config'));
    this.getData()
  },
  methods: {
    getData() {
      getInviteList().then(res => {
        this.loading = false;
        this.stat = res.data.stat;
        this.code = res.data.codes;
      })
      getInviteDetail().then(res => {
        this.list = res.data;
      })
    },
    copyInviteUrl(){
      this.$notification.open({
        message: '复制成功',
        icon: <a-icon type="smile" style="color: #108ee9" />,
      });
    },
    createInviteCode(){
      saveInviteCode().then(res => {
        if (res.data){
          this.$message.success('生成邀请码成功');
          window.location.reload()
        }
      })
    }
  },
  filters: {
    formatDate: function (value) {
      if (!value) {
        return false;
      }
      if (value.toString().length === 10) {
        value = value * 1000;
      }
      let date = new Date(value);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? ('0' + MM) : MM;
      let d = date.getDate();
      d = d < 10 ? ('0' + d) : d;
      let h = date.getHours();
      h = h < 10 ? ('0' + h) : h;
      let m = date.getMinutes();
      m = m < 10 ? ('0' + m) : m;
      let s = date.getSeconds();
      s = s < 10 ? ('0' + s) : s;

      return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
    },
    money(value) {
      return '￥' + (value / 100).toFixed(2);
    }
  }
}
</script>

<style scoped>

</style>