<template>
  <div class="wrapper" :class="{ 'nav-open': true }">
    <side-bar v-bind:config="config"
              v-bind:user="user"
      :background-color="sidebarBackground"
      short-title="Argon"
      title="Argon"
    >
      <template v-slot:links>
        <sidebar-item
          :link="{
            name: '仪表盘',
            icon: 'ni ni-tv-2 text-primary',
            path: '/dashboard',
          }"
        />
        <sidebar-item
            :link="{
            name: '使用文档',
            icon: 'ni ni ni-air-baloon text-primary',
            path: '/knowledge',
          }"
        />
      </template>
      <template v-slot:links-2>
        <sidebar-item
            :link="{
            name: '节点列表',
            icon: 'ni ni-app text-primary',
            path: '/subscribe',
          }"
        />
        <sidebar-item
            :link="{
            name: '购买订阅',
            icon: 'ni ni-bag-17 text-primary',
            path: '/plan',
          }"
        />
      </template>
      <template v-slot:links-3>
        <sidebar-item
            :link="{
            name: '我的订单',
            icon: 'ni ni-bullet-list-67 text-primary',
            path: '/order',
          }"
        />
        <sidebar-item
            :link="{
            name: '我的邀请',
            icon: 'ni ni-single-02 text-primary',
            path: '/invite',
          }"
        />
      </template>
      <template v-slot:links-4>
        <sidebar-item
            :link="{
            name: '个人中心',
            icon: 'ni ni-circle-08 text-primary',
            path: '/profile',
          }"
        />
        <sidebar-item
            :link="{
            name: '我的工单',
            icon: 'ni ni-email-83 text-primary',
            path: '/ticket',
          }"
        />
        <sidebar-item
            :link="{
            name: '流量明细',
            icon: 'ni ni-cloud-download-95 text-primary',
            path: '/traffic',
          }"
        />
      </template>
    </side-bar>
    <div class="main-content" :data="sidebarBackground">
      <dashboard-navbar v-bind:user="user"></dashboard-navbar>
      <div @click="toggleSidebar">
        <!-- your content here -->
        <router-view v-bind:user="user" class="layout"></router-view>
        <content-footer v-if="!$route.meta.hideFooter"></content-footer>
      </div>
    </div>
  </div>
</template>
<script>
import DashboardNavbar from "./DashboardNavbar.vue";
import SideBar from "../components/SideBar.vue";
import SidebarItem from "../components/SidebarItem.vue";
import ContentFooter from "./ContentFooter.vue";
import byval from "@/utils/byval";
import {getUserInfo} from "@/api/api";
import {getLoginConfig} from "@/api/auth";

export default {
  components: {
    DashboardNavbar,
    ContentFooter,
    SideBar,
    SidebarItem,
  },
  data() {
    return {
      sidebarBackground: "vue", //vue|blue|orange|green|red|primary
      user: {},
      config: {},
      sub: {},
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    toggleSidebar() {
      byval.$emit('sidebar')
    },
    getData(){
      getUserInfo().then(res => {
        this.user = res.data;
      })
      getLoginConfig().then(res => {
        this.config = res.data;
      })
    },
  },
};
</script>
<style>
.layout {
  min-height: calc(100vh - 4rem);
}
</style>
