<template>
  <div>
    <a-spin :spinning="loading">
      <base-header type="gradient-primary" class="pb-6 pb-8 pt-5 pt-md-8">

      </base-header>
      <div class="container-fluid mt--7">
        <div class="row">
          <div class="col-lg-6 card-wrapper ct-example">
            <div class="card" style="margin-bottom: 20px">
              <div class="card-header bg-transparent">
                <div class="row align-items-center">
                  <div class="col">
                    <h3 class="mb-0">我的钱包</h3>
                  </div>
                  <div class="col text-right">
                  </div>
                </div>
              </div>
              <!-- Card body -->
              <div class="card-body">
                <div class="row">
                  <div class="col-xl-6">
                    <a-statistic title="余额 (CNY)" :precision="2" :value="user.balance | money"
                                 :valueStyle="`font-size: 3rem`"/>
                  </div>
                </div>
                <div class="row">
                  <div class="col-xl-6">
                    <a-statistic title="可用佣金 (CNY)" :precision="2" :value="user.commission_balance | money"/>
                  </div>
                  <div class="col-xl-6">
                    <base-button type="info"
                                 outline
                                 icon="ni ni-money-coins"
                                 v-if="! config.withdraw_close"
                                 @click="modal2 = true"
                    >佣金提现
                    </base-button>
                    <base-button type="primary" outline icon="ni ni-bag-17" @click="modal1 = true">佣金划转</base-button>
                  </div>
                </div>
              </div>
            </div>
            <div class="card" style="margin-bottom: 20px">
              <div class="card-header bg-transparent">
                <div class="row align-items-center">
                  <div class="col">
                    <h3 class="mb-0">通知</h3>
                  </div>
                  <div class="col text-right">
                  </div>
                </div>
              </div>
              <!-- Card body -->
              <div class="card-body">
                <div class="mt-3">
                  <strong>到期邮件提醒:</strong>
                  <a-switch class="float-right" v-model="notice.remind_expire" default-checked @change="onChange('remind_expire')"/>
                </div>
                <div class="mt-3">
                  <strong>流量邮件提醒:</strong>
                  <a-switch class="float-right" v-model="notice.remind_traffic" default-checked @change="onChange('remind_traffic')"/>
                </div>
                <div class="mt-3" v-if="config.is_telegram">
                  <hr>
                  <strong>绑定Telegram</strong>
                  <base-button class="float-right" type="primary" size="sm" @click="bindTelegram">立即绑定</base-button>
                </div>
              </div>
            </div>
          </div>
          <div class="col-lg-6 card-wrapper ct-example">
            <div class="card" style="margin-bottom: 20px">
              <div class="card-header bg-transparent">
                <div class="row align-items-center">
                  <div class="col">
                    <h3 class="mb-0">修改密码</h3>
                  </div>
                  <div class="col text-right">
                    <base-button type="primary" @click="savePassword">保存</base-button>
                  </div>
                </div>
              </div>
              <!-- Card body -->
              <div class="card-body">
                <div class="form-group has-label">
                  <label class="form-control-label"> 旧密码 </label>
                  <input v-model="form.old_password" aria-describedby="addon-right addon-left" type="password"
                         placeholder="请输入旧密码"
                         class="form-control">
                </div>
                <div class="form-group has-label">
                  <label class="form-control-label"> 新密码 </label>
                  <input v-model="form.new_password" aria-describedby="addon-right addon-left" type="password"
                         placeholder="请输入新密码"
                         class="form-control">
                </div>
                <div class="form-group has-label">
                  <label class="form-control-label"> 确认新密码 </label>
                  <input v-model="form.new_password_2" aria-describedby="addon-right addon-left" type="password"
                         placeholder="请输入确认新密码"
                         class="form-control">
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </a-spin>
    <modal :show="modal1"
           :showClose="false"
           body-classes="p-0"
           modal-classes="modal-dialog-centered">
      <template v-slot:header>
        <h4 class="modal-title" id="modal-title-notification">推广佣金划转至余额</h4>
      </template>
      <div class="p-3">
        <base-alert type="primary">
          <span class="alert-inner--icon"><i class="ni ni-like-2"></i></span>
          <span class="alert-inner--text ml-2">划转后的余额仅用于消费使用</span>
        </base-alert>
        <div>
          <base-input label="当前推广佣金余额" :value="user.commission_balance | money" placeholder="Default input" disabled/>
          <div data-v-352d3bff="" class="form-group has-label"><label class="form-control-label"> 划转金额 </label>
            <input v-model="transfer_amount" aria-describedby="addon-right addon-left" placeholder="请输入需要划转到余额的金额"
                   class="form-control">
          </div>
        </div>
      </div>
      <template v-slot:footer>
        <base-button type="link"
                     class="ml-auto"
                     @click="modal1 = false">
          取消
        </base-button>
        <base-button type="primary" @click="onSubmit1">确定</base-button>
      </template>
    </modal>
    <modal :show="modal2"
           :showClose="false"
           body-classes="p-0"
           modal-classes="modal-dialog-centered">
      <template v-slot:header>
        <h4 class="modal-title">申请提现</h4>
      </template>
      <div class="p-3">
        <div>
          <div class="form-group">
            <label class="form-control-label">提现方式</label>
            <div class="has-label">
              <select class="form-control" v-model="withdraw_method">
                <option v-for="(item, i) in withdraw_methods" :value="item" :key="i">{{ item }}</option>
              </select>
            </div>
          </div>
          <div class="form-group has-label">
            <label class="form-control-label"> 提现账号 </label>
            <input v-model="withdraw_account" aria-describedby="addon-right addon-left" placeholder="请输入需要划转到余额的金额"
                   class="form-control">
          </div>
        </div>
      </div>
      <template v-slot:footer>
        <base-button type="link"
                     class="ml-auto"
                     @click="modal2 = false">
          取消
        </base-button>
        <base-button type="primary" @click="onSubmit2">确定</base-button>
      </template>
    </modal>
    <modal :show="modal3"
           :showClose="false"
           body-classes="p-0"
           modal-classes="modal-dialog-centered">
      <template v-slot:header>
        <h4 class="modal-title">绑定Telegram</h4>
      </template>
      <div class="p-3">
        <a-steps :v-model="current" @change="onChangeCurrent">
          <a-step title="第一步" :description="`打开Telegram搜索@${tg_name}`" />
          <a-step title="第二步" description="向机器人发送 /bind  命令" />
        </a-steps>
      </div>
      <template v-slot:footer>
        <base-button type="primary" @click="modal3 = false">知道了</base-button>
      </template>
    </modal>
  </div>
</template>

<script>
import {getUserInfo,
  transferCommission,
  getCommConfig,
  ticketWithdraw,
  changePassword,
  updateUserNotice,
  getBotInfo} from "@/api/api";

export default {
  name: "profile",
  components: {},
  data() {
    return {
      current: 0,
      user: {},
      config: {},
      form: {
        old_password: '',
        new_password: '',
        new_password_2: '',
      },
      notice: {
        remind_expire: 1,
        remind_traffic: 1,
      },
      loading: true,
      modal1: false,
      modal2: false,
      modal3: false,
      transfer_amount: 0,
      withdraw_methods: [],
      withdraw_method: '',
      withdraw_account: '',
      tg_name: ''
    }
  },
  mounted() {
    this.getData()
  },
  methods: {
    getData() {
      getUserInfo().then(res => {
        this.loading = false;
        this.user = res.data;
        this.notice = {
          remind_expire: res.data.remind_expire,
          remind_traffic: res.data.remind_traffic,
        }
      })
      getCommConfig().then(res => {
        this.config = res.data;
        if (res.data.withdraw_methods) {
          this.withdraw_methods = res.data.withdraw_methods;
          this.withdraw_method = res.data.withdraw_methods[0];
        }
      })
    },
    onSubmit1() {
      let transfer_amount = this.transfer_amount * 100;
      transferCommission({
        transfer_amount: transfer_amount
      }).then(res => {
        if (res.data) {
          this.$message.success('划转成功');
          this.modal1 = false;
          this.getData();
        }
      })
    },
    onSubmit2() {
      if (!this.withdraw_method) {
        this.$message.warning('请选择提现方式');
        return false;
      }
      if (!this.withdraw_account) {
        this.$message.warning('请输入提现账号');
        return false;
      }
      ticketWithdraw({
        withdraw_method: this.withdraw_method,
        withdraw_account: this.withdraw_account
      }).then(res => {
        if (res.data) {
          this.$message.success('提现成功');
          this.modal2 = false;
          this.getData();
        }
      })
    },
    savePassword() {
      if (!this.form.old_password) {
        this.$notification.warning({message: '请输入旧密码'});
        return false;
      }
      if (!this.form.new_password) {
        this.$notification.warning({message: '请输入新密码'});
        return false;
      }
      if (this.form.new_password !== this.form.new_password_2) {
        this.$notification.warning({message: '两次密码输入不一致'});
        return false;
      }
      changePassword(this.form).then(res => {
        if (res.data) {
          this.$message.success('修改成功');
        }
      });
    },
    onChange(type) {
      let up = {};
      if (type === 'remind_expire'){
         up = {
           remind_expire: this.notice.remind_expire ? 1 : 0,
         }
      } else {
        up = {
          remind_traffic: this.notice.remind_traffic ? 1 : 0,
        }
      }
      updateUserNotice(up).then(res => {
        if (res.data){
          this.$message.success('修改成功');
        }
      });
    },
    bindTelegram() {
      this.modal3 = true;
      getBotInfo().then(res => {
        if (res.data){
           this.tg_name = res.data.username;
           this.modal3 = true;
        }
      });
    },
    onChangeCurrent(current){
      this.current = current;
    }
  },
  filters: {
    formatDate: function (value) {
      if (!value) {
        return false;
      }
      if (value.toString().length === 10) {
        value = value * 1000;
      }
      let date = new Date(value);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? ('0' + MM) : MM;
      let d = date.getDate();
      d = d < 10 ? ('0' + d) : d;
      let h = date.getHours();
      h = h < 10 ? ('0' + h) : h;
      let m = date.getMinutes();
      m = m < 10 ? ('0' + m) : m;
      let s = date.getSeconds();
      s = s < 10 ? ('0' + s) : s;

      return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
    },
    money(value) {
      return value / 100;
    }
  }
}
</script>

<style scoped>

</style>