<template>
  <div>
    <a-spin :spinning="loading">
      <base-header type="gradient-primary" class="pb-6 pb-8 pt-5 pt-md-8">

      </base-header>
      <div class="container-fluid mt--7">
        <div class="row justify-content-center">
          <a-empty style="margin-top: 15%;margin-bottom: 15%" v-if="list.length === 0">
            <span slot="description"> 暂无订阅商品 </span>
          </a-empty>
          <div class="card card-pricing border-0 text-center mb-4"
               v-else
               style="margin-right: 1.5rem;"
               v-for="(item, index) in list"
               :key="index"
          >
            <div class="card-header bg-transparent">
              <h2 class="text-uppercase ls-1 text-primary py-3 mb-0">{{ item.name }}</h2>
            </div>
            <div class="card-body px-lg-7">
              <div class="display-2" v-if="item.month_price">{{ item.month_price | money }}</div>
              <div class="display-2" v-else-if="item.quarter_price">{{ item.quarter_price | money }}</div>
              <div class="display-2" v-else-if="item.half_year_price">{{ item.half_year_price | money }}</div>
              <div class="display-2" v-else-if="item.year_price">{{ item.year_price | money }}</div>
              <div class="display-2" v-else-if="item.two_year_price">{{ item.two_year_price | money }}</div>
              <div class="display-2" v-else-if="item.three_year_price">{{ item.three_year_price | money }}</div>
              <div class="display-2" v-else-if="item.onetime_price">{{ item.onetime_price | money }}</div>
              <span class="text-muted" v-if="item.month_price">月付</span>
              <span class="text-muted" v-else-if="item.quarter_price">季付</span>
              <span class="text-muted" v-else-if="item.half_year_price">半年付</span>
              <span class="text-muted" v-else-if="item.year_price">年付</span>
              <span class="text-muted" v-else-if="item.two_year_price">两年付</span>
              <span class="text-muted" v-else-if="item.three_year_price">三年付</span>
              <span class="text-muted" v-else-if="item.onetime_price">一次性</span>
              <p v-html="item.content" style="width: 250px"></p>
              <base-button icon="ni ni-cart" type="primary" class="mb-3" @click="purchase(item.id)">立即订阅</base-button>
            </div>
          </div>
        </div>
      </div>
    </a-spin>
  </div>
</template>

<script>
import {getPlanList} from "@/api/api";

export default {
  name: "knowledge",
  data() {
    return {
      list: [],
      loading: true,
    }
  },
  mounted() {
    this.getData()
  },
  methods: {
    getData() {
      getPlanList().then(res => {
        this.loading = false;
        this.list = res.data;
      })
    },
    purchase(id) {
      this.$router.push({name: '订阅详情', params: {id: id}});
    }
  },
  filters: {
    formatDate: function (value) {
      if (!value) {
        return false;
      }
      if (value.toString().length === 10) {
        value = value * 1000;
      }
      let date = new Date(value);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? ('0' + MM) : MM;
      let d = date.getDate();
      d = d < 10 ? ('0' + d) : d;

      return y + '-' + MM + '-' + d;
    },
    money(value) {
      return '￥' + (value / 100).toFixed(2);
    }
  }
}
</script>

<style scoped>

</style>