import request from "../utils/request";

export const getLoginConfig = params => {
    return request.get('api/v1/guest/comm/config', params).then(res => {
        return res.data
    })
};

export const getRegisterConfig = params => {
    return request.get('api/v1/passport/comm/config', params).then(res => {
        return res.data
    })
};

// 登录
export const login = params => {
    return request.post('api/v1/passport/auth/login', params).then(res => {
        return res.data
    })
};

// 发送邮件
export const sendEmail = params => {
    return request.post('api/v1/passport/comm/sendEmailVerify', params).then(res => {
        return res.data
    })
};

// 注册
export const register = params => {
    return request.post('api/v1/passport/auth/register', params).then(res => {
        return res.data
    })
};

// 重置密码
export const forgetPassword = params => {
    return request.post('api/v1/passport/auth/forget', params).then(res => {
        return res.data
    })
};