<template>
  <div class="row justify-content-center">
    <div class="col-lg-5 col-md-7">
      <div class="card bg-secondary shadow border-0">
        <div class="card-header bg-transparent">
          <div class="text-muted text-center mt-2 mb-3">
            <h2 style="color: #909399">用户注册</h2>
          </div>
        </div>
        <div class="card-body px-lg-5 py-lg-5">
          <div class="text-center text-muted mb-4">
            <small>Or sign up with credentials</small>
          </div>
          <form role="form">
            <div class="form-group input-group input-group-alternative">
              <div class="input-group-prepend"><span class="input-group-text"><i class="ni ni-email-83"></i></span>
              </div>
              <input aria-describedby="addon-right addon-left"
                     placeholder="邮箱"
                     focused=""
                     v-model="form.email"
                     class="form-control">
            </div>
            <div class="form-group input-group input-group-alternative" v-if="config.isEmailVerify">
              <div class="input-group-prepend"><span class="input-group-text"><i class="ni ni-hat-3"></i></span></div>
              <input aria-describedby="addon-right addon-left"
                     placeholder="验证码"
                     v-model="form.email_code"
                     class="form-control">
              <base-button type="primary">
                <span v-show="show" @click="sendMail">发送验证码</span>
                <span v-show="!show" class="count">{{ count }} s</span>
              </base-button>
            </div>
            <div class="form-group input-group input-group-alternative">
              <div class="input-group-prepend"><span class="input-group-text"><i
                  class="ni ni-lock-circle-open"></i></span></div>
              <input aria-describedby="addon-right addon-left" placeholder="密码"
                     v-model="form.password"
                     type="password"
                     class="form-control">
            </div>
            <div class="form-group input-group input-group-alternative">
              <div class="input-group-prepend"><span class="input-group-text"><i
                  class="ni ni-lock-circle-open"></i></span></div>
              <input aria-describedby="addon-right addon-left" placeholder="确认密码"
                     v-model="form.auth_password"
                     type="password"
                     class="form-control">
            </div>
            <div class="form-group input-group input-group-alternative">
              <div class="input-group-prepend"><span class="input-group-text"><i
                  class="ni ni-air-baloon"></i></span></div>
              <input aria-describedby="addon-right addon-left" :placeholder="invite_text"
                     v-model="form.invite_code"
                     class="form-control">
            </div>
            <div class="row my-4">
              <div class="col-12">
                <base-checkbox class="custom-control-alternative">
                  <span class="text-muted">
                    我已阅读并同意  <a :href="config.tos_url">服务条款</a></span>
                </base-checkbox>
              </div>
            </div>
            <div class="text-center">
              <base-button type="primary" class="my-4" icon="ni ni-active-40" @click="onSubmit">注册</base-button>
            </div>
          </form>
        </div>
      </div>
      <div class="row mt-3">
        <div class="col-6">
          <router-link to="/forgetPassword" class="text-light">
            <small>忘记密码</small>
          </router-link>
        </div>
        <div class="col-6 text-right">
          <router-link to="/login" class="text-light">
            <small>已有账号？请登录</small>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>
<script>

import {getRegisterConfig, sendEmail, register} from '../api/auth';

export default {
  name: "register",
  data() {
    return {
      form: {
        invite_code: "",
        email_code: "",
        email: "",
        password: "",
        auth_password: "",
      },
      config: {},
      invite_text: "",
      show: true,
      count: 60
    };
  },
  mounted() {
    if (this.$route.query.code){
      this.form.invite_code = this.$route.query.code;
    }
    this.getConfig();
  },
  methods: {
    getConfig() {
      getRegisterConfig().then(res => {
        this.config = res.data;
        if (res.data.isInviteForce) {
          this.invite_text = "邀请码(必填)";
        } else {
          this.invite_text = "邀请码(选填)";
        }
      })
    },
    sendMail() {
      if (!this.form.email) {
        this.$message.warning('请先输入邮箱')
        return false;
      }
      let reg = new RegExp("^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$");
      if (!reg.test(this.form.email)) {
        this.$message.warning('邮箱格式不正确')
        return false;
      }
      const TIME_COUNT = 60;
      sendEmail({email: this.form.email}).then(res => {
        if (res.data) {
          this.$message.success('发送成功');
          this.count = TIME_COUNT;
          this.show = false;
          this.timer = setInterval(() => {
            if (this.count > 0 && this.count <= TIME_COUNT) {
              this.count--;
            } else {
              this.show = true;
              clearInterval(this.timer);
              this.timer = null;
            }
          }, 1000);
        }
      });
    },
    onSubmit() {
      if (!this.form.email) {
        this.$message.warning('请先输入邮箱')
        return false;
      }
      if (!this.form.password) {
        this.$message.warning('请输入密码')
        return false;
      }
      if (this.form.auth_password !== this.form.password) {
        this.$message.warning('两次密码输入不一致')
        return false;
      }
      if (this.config.isInviteForce && !this.form.invite_code) {
        this.$message.warning('请输入邀请码')
        return false;
      }
      if (this.config.isEmailVerify && !this.form.email_code) {
        this.$message.warning('请输入邮箱验证码')
        return false;
      }
      register(this.form).then(res => {
         if (res.data){
            console.log(res.data)
         }
      })
    }
  }
};
</script>
<style></style>
