import VueRouter from 'vue-router'
import Vue from 'vue'
import DashboardLayout from "@/layout/DashboardLayout";
import AuthLayout from "@/layout/AuthLayout";

import Dashboard from "../views/Dashboard.vue";
import Login from "../views/Login.vue";
import Register from "../views/Register.vue";
import forgetPassword from "../views/forgetPassword.vue";
import knowledge from "../views/knowledge.vue";
import subscribe from "../views/subscribe.vue";
import plan from "../views/plan.vue";
import purchase from "../views/purchase.vue";
import checkout from "../views/checkout.vue";
import order from "../views/order.vue";
import invite from "../views/invite.vue";
import profile from "../views/profile.vue";
import ticket from "../views/ticket.vue";
import traffic from "../views/traffic.vue";

Vue.use(VueRouter)

const routes = [
    {
        path: "/",
        redirect: "login",
        component: AuthLayout,
        children: [
            {
                path: "/login",
                name: "用户登录",
                components: {default: Login},
            },
            {
                path: "/register",
                name: "用户注册",
                components: {default: Register},
            },
            {
                path: "/forgetPassword",
                name: "忘记密码",
                components: {default: forgetPassword},
            }
        ],
    },
    {
        path: "/",
        redirect: "/dashboard",
        component: DashboardLayout,
        children: [
            {
                path: "/dashboard",
                name: "首页",
                components: {default: Dashboard},
            },
            {
                path: "/knowledge",
                name: "使用文档",
                components: {default: knowledge},
            },
            {
                path: "/subscribe",
                name: "节点列表",
                components: {default: subscribe},
            },{
                path: "/plan",
                name: "购买订阅",
                components: {default: plan},
            },{
                path: "/plan/:id",
                name: "订阅详情",
                props: true,
                components: {default: purchase},
            },{
                path: "/order/:id",
                name: "订单详情",
                props: true,
                components: {default: checkout},
            },{
                path: "/order",
                name: "订单列表",
                components: {default: order},
            },{
                path: "/invite",
                name: "我的邀请",
                components: {default: invite},
            },{
                path: "/profile",
                name: "个人中心",
                components: {default: profile},
            },{
                path: "/ticket",
                name: "工单列表",
                components: {default: ticket},
            },{
                path: "/traffic",
                name: "流量明细",
                components: {default: traffic},
            },
        ]
    }
];

const router = new VueRouter({
    linkActiveClass: "active",
    routes,
});

const config = JSON.parse(localStorage.getItem('config'));

router.beforeEach((to, from, next) => {
    if (config){
        document.title = `${to.name} | ${config.app_name}`;
    } else {
        document.title = `${to.name}`;
    }
    next({query: to.query});
});

export default router;
