<template>
  <div class="row justify-content-center">
    <div class="col-lg-5 col-md-7">
      <div class="card bg-secondary shadow border-0">
        <div class="card-header bg-transparent pb-5">
          <div class="text-muted text-center mt-2 mb-3">
            <h2 style="color: #909399">用户登陆</h2>
          </div>
          <div class="btn-wrapper text-center">

          </div>
        </div>
        <div class="card-body px-lg-5 py-lg-5">
          <div class="text-center text-muted mb-4">
            <small>Or sign in with credentials</small>
          </div>
          <form role="form">
            <div class="form-group input-group input-group-alternative mb-3">
              <div class="input-group-prepend"><span class="input-group-text"><i class="ni ni-email-83"></i></span>
              </div>
              <input aria-describedby="addon-right addon-left" placeholder="邮箱" class="form-control"
                     v-model="form.email">
            </div>
            <div class="form-group input-group input-group-alternative mb-3"><!---->
              <div class="input-group-prepend"><span class="input-group-text"><i
                  class="ni ni-lock-circle-open"></i></span></div>
              <input aria-describedby="addon-right addon-left" placeholder="密码" type="password" class="form-control"
                     accept="" alt="" v-model="form.password">
            </div>
            <base-checkbox class="custom-control-alternative">
              <span class="text-muted">记住我</span>
            </base-checkbox>
            <div class="text-center">
              <base-button icon="ni ni-spaceship" type="primary" class="my-4" @click="onSubmit">登录</base-button>
            </div>
          </form>
        </div>
      </div>
      <div class="row mt-3">
        <div class="col-6">
          <a href="#" class="text-light"><small>忘记密码?</small></a>
        </div>
        <div class="col-6 text-right">
          <router-link to="/register" class="text-light"
          ><small>注册账号</small></router-link
          >
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {login} from "@/api/auth";

export default {
  name: "login",
  data() {
    return {
      form: {
        email: "",
        password: "",
      },
    };
  },
  mounted() {
  },
  methods: {
    onSubmit() {
      login({
        email: this.form.email,
        password: this.form.password,
      }).then(res => {
        if (res.data){
           this.$message.success("登录成功")
          setTimeout(()=>{
            this.$router.push({path: '/dashboard'})
          }, 1000);
        }
      })
    }
  }
};
</script>
<style></style>
