<template>
  <div>
    <a-spin :spinning="loading">
      <base-header type="gradient-primary" class="pb-6 pb-8 pt-5 pt-md-8">

      </base-header>
      <div class="container-fluid mt--7">
        <div class="row justify-content-center">
          <div class="card mb-4 col-sm-12 col-xl-8 mb-3">
            <div class="card-header">
              <h3 class="mb-0">配置订阅</h3>
            </div>
            <div class="card-body">
              <h3>{{ data.name }}</h3>
              <p v-html="data.content"></p>
            </div>
          </div>
        </div>
        <div class="row justify-content-center">
          <div class="card mb-4 col-sm-12 col-xl-8 mb-3">
            <div class="card-header">
              <h3 class="mb-0">订单金额</h3>
            </div>
            <div class="card-body">
              <div class="row">
                <div class="col-lg-6">
                  <a-radio-group v-model="cycle" @change="changeCycle">
                    <a-radio-button value="month_price" class="shop-button" v-if="data.month_price">
                      <h3>月付</h3>
                      <span>{{ data.month_price | money }}</span>
                    </a-radio-button>
                    <a-radio-button value="quarter_price" class="shop-button" v-if="data.quarter_price">
                      <h3>季付</h3>
                      <span>{{ data.quarter_price | money }}</span>
                    </a-radio-button>
                    <a-radio-button value="half_year_price" class="shop-button" v-if="data.half_year_price">
                      <h3>半年付</h3>
                      <span>{{ data.half_year_price | money }}</span>
                    </a-radio-button>
                    <a-radio-button value="year_price" class="shop-button" v-if="data.year_price">
                      <h3>年付</h3>
                      <span>{{ data.year_price | money }}</span>
                    </a-radio-button>
                    <a-radio-button value="two_year_price" class="shop-button" v-if="data.two_year_price">
                      <h3>两年付</h3>
                      <span>{{ data.two_year_price | money }}</span>
                    </a-radio-button>
                    <a-radio-button value="three_year_price" class="shop-button" v-if="data.three_year_price">
                      <h3>三年付</h3>
                      <span>{{ data.three_year_price | money }}</span>
                    </a-radio-button>
                    <a-radio-button value="onetime_price" class="shop-button" v-if="data.onetime_price">
                      <h3>一次性</h3>
                      <span>{{ data.onetime_price | money }}</span>
                    </a-radio-button>
                  </a-radio-group>
                </div>
                <div class="col-lg-6">
                  <a-input-search
                      placeholder="输入优惠卷"
                      enter-button="验证"
                      size="large"
                      :allowClear="true"
                      @search="onCheck"
                  >
                    <a-icon slot="prefix" type="account-book"/>
                  </a-input-search>
                  <div style="margin-top: 2rem">
                    <hr class="my-3"/>
                    <span style="font-size: large;">{{ data.name }} x {{ cycle_text }}</span>
                    <span style="font-size: large;float: right">{{ total | money }}</span>
                  </div>
                  <div v-if="discount_price">
                    <hr class="my-3"/>
                    <span style="font-size: large;">抵扣金额：{{ discount_price | money }}</span>
                    <hr class="my-3"/>
                  </div>
                  <div style="margin-top: 1rem">
                    <h2>总计：{{ total | money }}</h2>
                  </div>
                </div>
              </div>
            </div>
            <div class="card-footer">
              <button class="btn btn-icon btn-primary" type="button" style="float: right" @click="onSubmit">
                <span class="btn-inner--icon"><i class="ni ni-bag-17"></i></span>
                <span class="btn-inner--text">提交订单</span>
              </button>
            </div>
          </div>
        </div>
      </div>
    </a-spin>
  </div>
</template>

<script>
import {checkCoupon, getPlanList, saveOrder} from "@/api/api";

export default {
  name: "knowledge",
  data() {
    return {
      data: {},
      loading: true,
      cycle: 'month_price',
      cycle_text: '月付',
      cycleList: {
        month_price: '月付',
        quarter_price: '季付',
        half_year_price: '半年付',
        year_price: '年付',
        two_year_price: '两年付',
        three_year_price: '三年付',
        onetime_price: '一次性',
      },
      discount_price: 0,
      price: 0,
      total: 0,
      plan_id: 0,
      coupon_code: ''
    }
  },
  mounted() {
    this.plan_id = this.$route.params.id;
    this.getData(this.plan_id)
  },
  methods: {
    getData(id) {
      getPlanList({id: id}).then(res => {
        this.loading = false;
        this.data = res.data;
        if (res.data.month_price) {
          this.price = res.data.month_price;
          this.total = res.data.month_price;
          this.cycle_text = '月付';
          this.cycle = 'month_price';
        } else if (res.data.quarter_price) {
          this.price = res.data.quarter_price;
          this.total = res.data.quarter_price;
          this.cycle_text = '季付';
          this.cycle = 'quarter_price';
        } else if (res.data.half_year_price) {
          this.price = res.data.half_year_price;
          this.total = res.data.half_year_price;
          this.cycle_text = '半年付';
          this.cycle = 'half_year_price';
        } else if (res.data.year_price) {
          this.price = res.data.year_price;
          this.total = res.data.year_price;
          this.cycle_text = '年付';
          this.cycle = 'year_price';
        } else if (res.data.two_year_price) {
          this.price = res.data.two_year_price;
          this.total = res.data.two_year_price;
          this.cycle_text = '两年付';
          this.cycle = 'three_year_price';
        } else if (res.data.two_year_price) {
          this.price = res.data.three_year_price;
          this.total = res.data.three_year_price;
          this.cycle_text = '三年付';
          this.cycle = 'three_year_price';
        } else if (res.data.onetime_price) {
          this.price = res.data.onetime_price;
          this.total = res.data.onetime_price;
          this.cycle_text = '一次性';
          this.cycle = 'onetime_price';
        }
      })
    },
    onCheck(value) {
      checkCoupon({
        code: value,
        plan_id: this.plan_id
      }).then(res => {
        this.coupon_code = res.data.code;
        if (res.data.type === 1) {
          this.total = (this.price - res.data.value).toFixed(2);
          this.discount_price = -res.data.value;
        } else {
          let credit = parseFloat((100 - res.data.value) / 100);
          this.total = (this.price * credit).toFixed(2);
          this.discount_price = (this.total - this.price).toFixed(2);
        }
      })
    },
    onSubmit() {
      let param = {
        cycle: this.cycle,
        plan_id: this.plan_id
      };
      if (this.coupon_code) {
        param['coupon_code'] = this.coupon_code;
      }
      saveOrder(param).then(res => {
        if (res.data) {
          this.$router.push({name: '订单详情', params: {id: res.data}});
        }
      })
    },
    changeCycle() {
      this.cycle_text = this.cycleList[this.cycle];
      this.total = this.data[this.cycle];
      this.price = this.data[this.cycle];
    }
  },
  filters: {
    formatDate: function (value) {
      if (!value) {
        return false;
      }
      if (value.toString().length === 10) {
        value = value * 1000;
      }
      let date = new Date(value);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? ('0' + MM) : MM;
      let d = date.getDate();
      d = d < 10 ? ('0' + d) : d;

      return y + '-' + MM + '-' + d;
    },
    money(value) {
      return '￥' + (value / 100).toFixed(2);
    }
  }
}
</script>

<style scoped>
.shop-button {
  margin: 0.5rem;
  padding: 20px 40px;
  height: unset;
  box-shadow: 0 2px 10px 0 rgb(0 0 0 / 5%);
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #d9d9d9;
  text-align: center;
}
</style>