<template>
  <div>
    <base-header type="gradient-primary" class="pb-6 pb-8 pt-5 pt-md-8">
      <div class="row justify-content-center">

      </div>
    </base-header>
    <div class="container-fluid mt--9">
      <a-spin :spinning="loading">
        <div class="row justify-content-center">
          <div class="col-lg-10 card-wrapper ct-example">
            <!-- 我的订阅 -->
            <div class="card" style="margin-bottom: 20px">
              <div class="card-header">
                <h3 class="mb-0">我的订阅</h3>
              </div>
              <div class="card-body" v-if="sub.plan">
                <div class="row">
                  <div class="col-lg-8">
                    <strong>
                      {{ sub.plan.name }}
                    </strong>
                    <p>
                      于 {{ sub.expired_at | formatDate }} 到期，距离到期还有 {{ sub.expired_at | intervalDay }} 天，{{
                        sub.reset_day
                      }}
                      日后重置流量
                    </p>
                    <base-progress show-label type="primary"
                                   :height="8"
                                   :value="parseInt(sub.u / sub.transfer_enable)"
                                   label="已用流量"></base-progress>
                    <p>
                      已用 {{ sub.u | bytesToSize }} / 总计 {{ sub.transfer_enable | bytesToSize }}
                    </p>
                  </div>
                  <div class="col-lg-4 text-center" style="padding: 4rem;">
                    <base-button type="primary" @click="modal2 = true">
                      <i class="ni ni-atom"></i>
                      重置订阅
                    </base-button>
                  </div>
                </div>
              </div>
              <div class="card-body text-center" v-else>
                <a-empty
                    image="https://gw.alipayobjects.com/mdn/miniapp_social/afts/img/A*pevERLJC9v0AAAAAAAAAAABjAQAAAQ/original"
                    :image-style="{height: '60px'}"
                >
                  <span slot="description"> 暂无订阅 </span>
                  <a-button type="primary" @click="$router.push({path: '/plan'})">
                    添加订阅
                  </a-button>
                </a-empty>
              </div>
            </div>
            <!-- 节点列表 -->
            <div class="card" style="margin-bottom: 20px">
              <div class="card-header">
                <h3 class="mb-0">节点状态</h3>
              </div>
              <div class="card-body text-center">
                <div v-if="serveList.length === 0">
                  <a-empty>
                    <span slot="description"> 暂无数据 </span>
                  </a-empty>
                </div>
                <div class="row" v-if="serveList">
                  <div class="col-sm-12 col-xl-6 mb-3"
                       style="cursor: pointer;"
                       v-for="(item, index) in serveList"
                       :key="index">
                    <div class="card card-custom cursor_onclick rounded-lg shadow"
                         v-clipboard:copy="item.link"
                         v-clipboard:success="copySublink">
                      <div class="card-body pl-4">
                        <ul class="list-unstyled user-details list-unstyled-border list-unstyled-noborder">
                          <li class="media">
                            <img alt="image" class="mr-3 rounded-circle" width="50"
                                 :src="`https://cdn.jsdelivr.net/gh/BobCoders/metron-assets@3.0.2/metron/media/flags/1x1_zh_cn/${item.flag}.svg`">
                            <div class="media-body">
                              <div class="media-title">
                                <span class="label label-dot label-xl"
                                      :class="item.last_check_at ? 'label-success' : 'label-danger'"></span>
                                {{ item.name }}
                              </div>
                              <div class=" text-job text-muted">
                                <a-tag color="cyan" v-for="(v, i) in item.tags" :key="i">
                                  {{ v }}
                                </a-tag>
                              </div>
                            </div>
                            <div class="media-items">
                              <div class="media-item">
                                <div class="media-value">x {{ item.rate }}</div>
                                <div class="media-label">倍率</div>
                              </div>
                            </div>
                          </li>
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </a-spin>
    </div>
    <modal :show="modal2" modal-classes="modal-dialog-centered" :showClose="true">
      <template v-slot:header>
        <h6 class="modal-title" id="modal-title-notification">提示</h6>
      </template>
      <div class="py-3 text-center">
        <i class="ni ni-bell-55 ni-3x"></i>
        <h4 class="heading mt-4">确定要重置订阅信息？</h4>
        <p>如果你的订阅地址或信息泄露可以进行此操作。重置后你的UUID及订阅将会变更，需要重新进行订阅。</p>
      </div>
      <template v-slot:footer>
        <base-button type="link"
                     class="ml-auto"
                     @click="modal2 = false">
          取消
        </base-button>
        <base-button type="primary" @click="resetSublink">确定</base-button>
      </template>
    </modal>
  </div>
</template>

<script>
import {getSubscribe, getSeverList} from "@/api/api";
import {resetSecurity} from "../api/api";

export default {
  name: "knowledge",
  components: {},
  data() {
    return {
      serveList: [],
      loading: true,
      modal2: false,
      sub: {},
    }
  },
  mounted() {
    this.getData()
  },
  methods: {
    getData() {
      getSubscribe().then(res => {
        this.sub = res.data;
        this.loading = false;
      })
      getSeverList().then(res => {
        let data = [];
        let flags = ['香港', '美国', '日本', '中国', '俄罗斯', '韩国', '英国', '新加坡', '马来西亚', '台湾', '加拿大', '菲律宾', '德国', '印度', '南非', '卢森堡', '巴西', '意大利', '法国', '泰国', '爱尔兰'];
        res.data.forEach(function (item) {
          item.flag = item.name.split(" ")[0];
          if (flags.indexOf(item.flag) === -1) {
            item.flag = 'un';
          }
          item.tags = JSON.parse(item.tags)
          data.push(item)
        });
        this.serveList = data;
      });
    },
    copySublink() {
      this.$notification.open({
        message: '链接复制成功',
        icon: <a-icon type="smile" style="color: #108ee9"/>,
      });
    },
    resetSublink(){
      resetSecurity().then(res => {
        this.modal2 = false;
         if (res.data){
            this.$message.success('重置订阅连接成功');
         }
      });
    }
  },
  filters: {
    formatDate: function (value) {
      if (!value) {
        return false;
      }
      if (value.toString().length === 10) {
        value = value * 1000;
      }
      let date = new Date(value);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? ('0' + MM) : MM;
      let d = date.getDate();
      d = d < 10 ? ('0' + d) : d;

      return y + '-' + MM + '-' + d;
    },
    intervalDay: function (value) {
      let timestamp = (Date.parse(new Date())) / 1000;//计算当前时间戳 (毫秒级)
      let nTime = value - timestamp;

      return Math.floor(nTime / 86400);
    },
    bytesToSize(bytes) {
      if (bytes === 0) return '0 B';
      var k = 1024, // or 1024
          sizes = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'],
          i = Math.floor(Math.log(bytes) / Math.log(k));

      return (bytes / Math.pow(k, i)).toPrecision(3) + ' ' + sizes[i];
    },
  }
}
</script>

<style scoped>
.label.label-xl.label-dot {
  line-height: 10px;
  min-height: 10px;
  min-width: 10px;
  height: 10px;
  width: 10px;
}

.label.label-dot {
  display: inline-block;
  font-size: 0 !important;
  vertical-align: middle;
  text-align: center;
}

.label {
  padding: 0;
  margin: 0;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  font-size: 0.8rem;
  background-color: #ECF0F3;
  color: #464E5F;
  font-weight: 400;
}

.label.label-danger {
  color: #ffffff;
  background-color: #F64E60;
}

.label.label-success {
  color: #ffffff;
  background-color: #1BC5BD;
}
</style>